@import url(https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@300;400;500;600;700&family=Bellefair&display=swap);
:root {
  --white: #ffffff;
  --grey: #d2d8f9;
  --black: #0c0e18;
  --font_primary: "Barlow Condensed", sans-serif;
  --gray: rgb(143, 135, 135);
  --font_sec: "Bellefair", serif;
  --font_third: "Barlow", sans-serif;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.3s ease;
}
body {
  font-size: 16px;
  overflow: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.row {
  flex-direction: row;
}
.column {
  flex-direction: column;
}

.role {
  text-transform: uppercase;
  color: rgb(143, 135, 135);
  color: var(--gray);
  font-family: "Barlow", sans-serif;
  font-family: var(--font_third);
  margin-bottom: 0.5rem;
}

.heading {
  text-transform: uppercase;
  color: #ffffff;
  color: var(--white);
  font-family: "Barlow", sans-serif;
  font-family: var(--font_third);
  font-size: 1.1rem;
  letter-spacing: 0.03rem;
}

.lead {
  font-family: "Barlow", sans-serif;
  font-family: var(--font_third);
  font-size: 0.85rem;
  text-align: center;
  color: #d2d8f9;
  color: var(--grey);
  letter-spacing: 0.05rem;
  margin: 1rem 0;
  font-weight: 400;
}

.small {
  text-transform: uppercase;
  font-family: "Barlow", sans-serif;
  font-family: var(--font_third);
  font-size: 0.75rem;
  text-align: center;
  color: #d2d8f9;
  color: var(--grey);
  letter-spacing: 0.05rem;
  margin: 1rem 0;
  font-weight: 400;
}

header {
  min-height: 100vh;
  overflow: hidden;
}

.home_bg {
  background: url(/space-tourism-website-challenge/static/media/background-home-tablet.bc9efcc3.jpg) no-repeat;
  position: relative;
  background-position: center;
}
/* Hero Section styling*/

.hero {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
  max-width: 600px;
  margin: auto;
}

.hero .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
}

.hero .m_heading {
  color: var(--grey);
  text-transform: uppercase;
  font-family: var(--font_primary);
  letter-spacing: 0.09rem;
  font-size: 1.2rem;
  font-weight: 400;
}

.hero .content .heading {
  font-size: 5rem;
  text-transform: uppercase;
  color: var(--white);
  font-family: var(--font_sec);
  font-weight: 400;
  margin: 1rem 0 1.2rem 0;
}

.hero .content .description {
  color: var(--grey);
  font-family: var(--font_third);
  font-weight: 400;
  text-align: center;
  font-size: 1rem;
  line-height: 1.3rem;
  padding: 0 1.3rem;
}

.hero .explore {
  width: 9rem;
  height: 9rem;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  cursor: pointer;
  transition: border 0.5s ease-in-out;
  position: relative;
  z-index: 2;
}

.hero .explore::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: white;
  border-radius: 50%;
  z-index: -1;
  transition: all 1s ease;
}

.hero .explore:hover::before {
  background: rgba(255, 255, 255, 0.6);
  width: 150%;
  height: 150%;
}
.hero .explore span {
  font-size: 1.5rem;
  text-transform: uppercase;
  color: var(--black);
  font-family: var(--font_sec);
  font-weight: 400;
}

@media screen and (min-width: 600px) {
  .home_bg {
    background: url(/space-tourism-website-challenge/static/media/background-home-desktop.cdf3864e.jpg) no-repeat;
  }
  .hero {
    flex-direction: row;
    max-width: 900px;
    margin: auto;
  }
  .hero .content {
    align-items: unset;
    max-width: 25rem;
    padding-right: 0;
  }
  .hero .content .m_heading {
    font-size: 1.8rem;
    padding-left: 3rem;
  }
  .hero .content .heading {
    font-size: 7rem;
    text-align: left;
    padding-left: 3rem;
  }
  .hero .content .description {
    text-align: left;
    padding-left: 3rem;
  }
  .hero .explore {
    margin: 0 1.5rem;
  }
}

@media screen and (min-width: 700px) {
  .hero .content {
    max-width: 35rem;
  }
  .hero .content .description {
    max-width: 26rem;
  }
  .hero .explore {
    margin: 0 3rem 0 3rem;
    width: 12rem;
    height: 12rem;
  }
}

@media screen and (min-width: 900px) {
  .hero .explore {
    margin: 0 3rem 0 15rem;
  }
}

nav {
  position: absolute;
  width: 100%;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0 1rem 2rem;
}

nav .logo {
  cursor: pointer;
}
nav .logo img {
  width: 35px;
  height: 35px;
}

/*Menu items Styling*/

nav .menu {
  color: white;
  cursor: pointer;
  z-index: 100;
  padding: 1rem 2rem;
}

nav .mobile_menu {
  display: flex;
  position: absolute;
  top: 0;
  right: -50rem;
  width: 17rem;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.1);
  z-index: 10;
  -webkit-backdrop-filter: blur(11px);
          backdrop-filter: blur(11px);
  /* transition: all 0.2s ease; */
}

nav .mobile_menu.active {
  right: 0;
}

nav .mobile_menu .menu_items {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
  width: 100%;
}
nav .mobile_menu .menu_items .menu_item {
  width: 100%;
  padding: 1rem 1.7rem;
  font-family: var(--font_primary);
  font-size: 1.1rem;
  /* transition: all 0.5s ease; */
}
nav .mobile_menu .menu_items .menu_item:hover {
  background: rgba(255, 255, 255, 0.2);
  cursor: pointer;
}
nav .mobile_menu .menu_items .menu_item span {
  font-weight: 600;
  margin-right: 0.3rem;
}

.text-link {
  color: inherit;
  text-decoration: inherit;
}

@media screen and (min-width: 600px) {
  nav .menu {
    display: none;
  }
  nav .mobile_menu {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    right: 0;
    flex-direction: row;
    width: 500px;
    top: 0;
    height: 4rem;
  }
  nav .mobile_menu .menu_items {
    flex-direction: row;
    width: 100%;
    margin-top: 0;
    height: 100%;
    justify-content: flex-start;
  }
  nav .mobile_menu .menu_items .menu_item {
    font-family: var(--font_primary);
    font-size: 1.1rem;
    transition: all 0.1s ease;
    height: 100%;
    padding: 1rem 1.3rem;
  }
  /* nav .mobile_menu .menu_items .menu_item.active {
    border-bottom: 3px solid white;
  } */
  nav a.active li {
    border-bottom: 3px solid white;
  }
}

@media screen and (min-width: 900px) {
  nav .mobile_menu {
    width: 700px;
  }
}

header {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}
.dest_bg {
  background: url(/space-tourism-website-challenge/static/media/background-destination-tablet.8f55d346.jpg)
    no-repeat;
  background-size: cover;
  background-position: center;
}

/* Hero Section styling*/

.dest_hero {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 6rem;
  max-width: 600px;
  margin: auto;
}

.dest_hero .m_heading {
  color: var(--grey);
  text-transform: uppercase;
  font-family: var(--font_primary);
  letter-spacing: 0.12rem;
  font-size: 1.2rem;
  font-weight: 400;
  text-align: center;
  width: 100%;
  padding: 0 2rem;
}
.dest_hero .m_heading > span {
  color: var(--gray);
}

.dest_hero .content {
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
  width: 100%;
  max-width: 420px;
}

/* styling sat section */
.dest_hero .content .sat {
  margin-top: 0.8rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dest_hero .content .sat .img_box {
  width: 150px;
  height: 150px;
  margin: 0 1rem 0.6rem 1rem;
}

.dest_hero .content .sat .img_box img {
  width: 100%;
}

/* sat info */
.dest_hero .content .sat .sat_info {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.dest_hero .content .sat .sat_info .sat_nav {
  margin: 0 0 0.6rem 0;
}

.sat_items {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.sat_item {
  padding: 0.2rem 0.3rem;
  border-bottom: 2px solid transparent;
  transition: all 0.5s ease;
  text-transform: uppercase;
  font-family: var(--font_third);
  font-size: 0.8rem;
  font-weight: 500;
  letter-spacing: 0.04rem;
  color: var(--white);
  cursor: pointer;
}

.sat_item:hover {
  border-bottom: 2px solid var(--white);
}

.sat_name {
  text-align: center;
  text-transform: uppercase;
  font-size: 2.7rem;
  font-weight: 500;
  font-family: var(--font_third);
  color: var(--white);
}

.sat_desc {
  font-family: var(--font_primary);
  font-size: 0.8rem;
  text-align: center;
  color: var(--grey);
  letter-spacing: 0.03rem;
  margin-bottom: 0.8rem;
  min-height: 80px;
}

.sat_info hr {
  color: var(--gray);
  width: 85%;
  margin: auto;
}

/* Styling travel info distance and time */
.travel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 0.8rem;
}

.travel .distance {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.travel_title {
  text-transform: uppercase;
  font-family: var(--font_third);
  font-size: 0.75rem;
  font-weight: 500;
  letter-spacing: 0.04rem;
  color: var(--white);
  padding-bottom: 0.4rem;
}

.travel_info {
  text-transform: uppercase;
  font-family: var(--font_third);
  font-size: 1.2rem;
  font-weight: 600;
  letter-spacing: 0.04rem;
  color: var(--white);
  padding-bottom: 0.5rem;
}
/*! End of Costum Styling */

@media screen and (min-width: 600px) {
  .dest_bg {
    background: url(/space-tourism-website-challenge/static/media/background-destination-desktop.468b922c.jpg)
      no-repeat;
  }

  .dest_hero {
    padding-top: 8rem;
    min-width: 600px;
  }
  .dest_hero .m_heading {
    margin-bottom: 1.5rem;
  }

  .dest_hero .content {
    max-width: 600px;
  }
  .dest_hero .content .sat {
    flex-direction: row;
  }
  .dest_hero .content .sat .img_box {
    flex: 1 1;
  }
  .dest_hero .content .sat .sat_info {
    flex: 1 1;
  }
  .dest_hero .content .sat .img_box {
    width: 250px;
    height: 250px;
    margin: 0 2rem 0.6rem 1rem;
  }
  .dest_hero .m_heading,
  .sat_name,
  .sat_desc,
  .distance,
  .travel_title,
  .travel_info {
    text-align: left;
  }
  .sat_items {
    justify-content: space-between;
    max-width: 70%;
  }
  .sat_desc {
    margin-bottom: 2rem;
  }
  .sat_info hr {
    width: 100%;
  }
  .travel {
    flex-direction: row;
    justify-content: space-between;
  }
  .sat_item {
    font-size: 0.9rem;
  }
  .sat_name {
    font-size: 2.8rem;
  }
  .sat_desc {
    font-size: 0.9rem;
  }
}

@media screen and (min-width: 900px) {
  .dest_hero .content {
    min-width: 900px;
  }
  .dest_hero .content .sat .img_box {
    margin: 0 5rem 0.6rem 1rem;
    width: 390px;
    height: 390px;
  }
  .sat_items {
    width: 60%;
  }
  .dest_hero .content .sat .sat_info {
    max-width: 350px;
  }
  .dest_hero {
    min-width: 900px;
  }
  .sat_item {
    font-size: 1rem;
  }
  .sat_name {
    font-size: 3rem;
  }
  .sat_desc {
    font-size: 1rem;
  }
}

